import React, { Component } from 'react';
import Checkbox from '../Checkbox/Checkbox.js';
import Rating from 'react-rating';

class Filter extends Component{
  constructor(props) {
    super(props);
    this.state = { 
      isRate: 1,     
      isPool: false,
      isName: '',
    };        
  }

  filterRate = (rate) => {       
    const newRate = rate;    
    this.setState(()=>({isRate: newRate}));
    this.props.applyFilter(newRate, this.state.isPool, this.state.isName);    
  } 

  filterPool = ()=>{ 
    const newIsPool = !this.state.isPool;                
    this.setState((prevState) => ({
      isPool: !prevState.isPool
      //isPool: newIsPool
    }));    
    this.props.applyFilter(this.state.isRate, newIsPool,this.state.isName);    
  }

  filterName = (event) => {       
    const newName = event.target.value.toLowerCase();    
    this.setState(()=>({isName: newName}));
    this.props.applyFilter(this.state.isRate, this.state.isPool, newName);    
  }

  

  // formChange =()=>{    
  //   this.props.applyFilter(this.state.isPool, this.state.isName);
  // }  

  render(){ 
    const { isRate, isPool, isName } = this.state;
    console.log("filter");
    console.log(isRate, isPool, isName);
    //const { applyFilter } = this.props;   
    return (
      <div className="Filter">
        <form>
          <div className="filter-section">
            <h3 className="section-title">
              Stars
            </h3>
            <div className="rating-container">
              <Rating
                className="Rating"
                start={0}
                stop={5}                
                onChange={(rate) => (this.filterRate(rate))}
                initialRating={isRate}
                emptySymbol={
                  ['0+',2,3,4,5].map(n =>
                    <div className="rating-item-box">
                      <img src="./img/star.png" className="icon" alt="star"/>
                      <span className="icon-text">{n}</span>
                    </div>
                  )            
                }
                fullSymbol={
                  ['0+',2,3,4,5].map(n =>
                    <div className="rating-item-box-ok">
                      <img src="./img/starOk.png" className="icon" alt="Full star"/>
                      <span className="icon-text-ok">{n}</span>
                    </div>
                  ) 

                }
              />
            </div>            
          </div>
          <div className="filter-section">
            <h3 className="section-title">
              Freebies
            </h3> 
            <Checkbox        
              handleChecked = {this.filterPool}
            />
          </div>
          <div className="filter-section">            
            <h3 className="section-title">
              Hotel Name
            </h3>
            <div className="search-input-container">
              <input className="search-input"
                type="text"
                placeholder="Hotel Name"
                onChange = {this.filterName}        
              />
            </div>
          </div>
        </form>
      </div>
    )
  }
}

export default Filter;