import React, { Component } from 'react';

class CheckBox extends Component{
  constructor(props) {
    super(props);
    this.state = {
      isChecked: false
    };
    //this.handleChecked = this.handleChecked.bind(this); //Sometimes I prefer use arrow functions
  }

  intHandleChecked =()=> {
    //this.setState({isChecked: !this.state.isChecked});
    this.setState((prevState) => ({
      isChecked: !prevState.isChecked
    }));
  }

  render(){
    // console.log("checkbox");
    // console.log(this.state.isChecked);
    const { handleChecked = ()=>{} } = this.props;

    return (
      <div className="Checkbox"> 
        <label className="label-checkbox">
          <input type="checkbox" className="input" checked={this.state.isChecked} onChange={(event)=>{ this.intHandleChecked(); handleChecked(event);}}/>
          <div className="custom-checkbox"></div>         
          <span className="semi-bold">Has Pool</span>          
        </label>
      </div>
    )

  }
}

export default CheckBox;