import React, { Component } from 'react';
import Hotels from './data.json';
import HotelList from './ui/components/HotelList/HotelList.js';
import Filter from './ui/components/Filter/Filter.js';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      hotels: [],
      isLoading: false,      
      filteredHotels: [],      
    };
  }
  
  componentDidMount() {
    this.setState({ isLoading: true });

    // fetch('')
    //   .then(response => {
    //     if (response.ok) {
    //       return response.json();
    //     } else {
    //       throw new Error('Something went wrong ...');
    //     }
    //   })
    //   .then(data => this.setState({ hotels: data.hotels, isLoading: false }))
    //   .catch(error => this.setState({ error, isLoading: false }));
    this.setState({ hotels: Hotels.hotels, filteredHotels: Hotels.hotels, isLoading: false })                 
  }
  
  applyFilter =( isRate, isPool, isName )=>{
    let filteredList = this.state.hotels;
    console.log("App");
    console.log(isRate, isPool, isName);
    filteredList = filteredList.filter((item)=>{         
      return ( ( item.rate >= isRate) && (isPool ? item.hasPool === 'true' : true) && (~item.name.toLowerCase().search(isName)) )
      //return ( item.name.toLowerCase().search(isName) !== -1 ) 
      //return ( isPool ? item.hasPool === 'true' : true )     
    });           
    this.setState(() => ({filteredHotels: filteredList}));
  }

  render() {
    const { filteredHotels, isLoading } = this.state;    
    // console.log("hotels");   
    // console.log(this.state.hotels);    
    // console.log("filteredHotels");
    // console.log(this.state.filteredHotels); 
    

    if (isLoading) {
      return <p>Loading ...</p>;
    }

    return (
      <div className="App">
        <div className="logined-body">      
          <div className="Sidebar">
            <Filter
              applyFilter={this.applyFilter}
            />
          </div>
          <HotelList 
            filteredHotels={filteredHotels}
          />                      
        </div>      
      </div>
    );
  }
}

export default App;
