import React, { Component } from 'react';
import HotelCard from '../HotelCard/HotelCard.js';

class HotelList extends Component {
  constructor(props) {
    super(props);
    this.state = {            
    };
  }  

  render() {
    const { filteredHotels } = this.props;
    //console.log(filteredHotels);
    return (      
      <div className="HotelList">
        {
          filteredHotels&&filteredHotels.map( (hotel, index) => (
            <div className="hotelCard-container" key={index}>
              <HotelCard
                hotel={hotel}
              />
            </div>
          ))
        }
      </div>          
    );
  }
}

export default HotelList;


