import React, { Component } from 'react';

class HotelCard extends Component{
  constructor(props) {
    super(props);
    this.state = { 
      isDetailOpen: false,
    };    
  }

  handleDetail =()=> {
    this.setState((prevState) => ({
      isDetailOpen: !prevState.isDetailOpen
    }));
  }

  render(){    
    const { hotel } = this.props; 
    const { isDetailOpen } = this.state;

    return (
      <div className="HotelCard">
        <div className="main-content">
          <div className="img-container">
            <img src={hotel.img} alt="hotel"/>
          </div>
          <div className="shot-info-container">
            <div className="name-container">
              <span className="hotel-name">{hotel.name}</span>
            </div>
            <div className="rate-container">                  
              {/*<span>Rate: {hotel.rate}</span>*/}
              <div className="rate-stars-container">
                {
                  // [0,1,2,3,4]
                  Array.from({ length: 5 }, (v, i) => i).map( (number, index) => (                        
                    hotel.rate && hotel.rate > number ? (
                      <div className="rate-star-box" key={index}>
                        <svg className="rateOk" version="1.1" viewBox="0 0 19.481 19.481" enableBackground="new 0 0 19.481 19.481" width="20px" height="20px">
                          <g>
                            <path d="m10.201,.758l2.478,5.865 6.344,.545c0.44,0.038 0.619,0.587 0.285,0.876l-4.812,4.169 1.442,6.202c0.1,0.431-0.367,0.77-0.745,0.541l-5.452-3.288-5.452,3.288c-0.379,0.228-0.845-0.111-0.745-0.541l1.442-6.202-4.813-4.17c-0.334-0.289-0.156-0.838 0.285-0.876l6.344-.545 2.478-5.864c0.172-0.408 0.749-0.408 0.921,0z" fill="#000000"/>
                          </g>
                        </svg>
                      </div>
                    ):(
                      <div className="rate-star-box" key={index}>
                        <svg className="rate" version="1.1" viewBox="0 0 19.481 19.481" enableBackground="new 0 0 19.481 19.481" width="20px" height="20px">
                          <g>
                            <path d="m10.201,.758l2.478,5.865 6.344,.545c0.44,0.038 0.619,0.587 0.285,0.876l-4.812,4.169 1.442,6.202c0.1,0.431-0.367,0.77-0.745,0.541l-5.452-3.288-5.452,3.288c-0.379,0.228-0.845-0.111-0.745-0.541l1.442-6.202-4.813-4.17c-0.334-0.289-0.156-0.838 0.285-0.876l6.344-.545 2.478-5.864c0.172-0.408 0.749-0.408 0.921,0z" fill="#666666"/>
                          </g>
                        </svg>
                      </div> 
                    )                                            
                  ))
                }                    
              </div>                  
            </div>
          </div>
          <div className="action-container">
            <button
              className="detail-button"
              onClick={this.handleDetail}
            >
              View Detail
            </button>  
          </div>
        </div>

        <div className={`detail-content ${isDetailOpen ? 'opened' : ''}`}>          
          {
            hotel.description ?
            <div className="description-container">
              <p className="description">
                {hotel.description}
              </p>
            </div>
            :
            <div className="description-container">
              <p className="description">No description</p>
            </div>
          }
          
          {
            hotel.hasPool ? 
              <div className="hasPool-container">              
                <span className="hasPool">
                  Hotel has a pool: YES
                </span>                              
              </div>
            :
              <div className="hasPool-container">              
                <span className="hasPool">
                  Hotel has a pool: NO
                </span>                              
              </div>
          }
          {
            hotel.price&&<div className="price-container">
              {hotel.price.single&&<span className="price">
                single price: {hotel.price.single}
              </span>} 
              {hotel.price.double&&<span className="price">
                double price: {hotel.price.double}
              </span>}
              {hotel.price.twin&&<span className="price">
                twin price: {hotel.price.twin}
              </span>}
            </div>                
          } 
          {
            hotel.address&&<div className="address-container">              
              <span className="address">
                {hotel.address}
              </span>                              
            </div>
          }  
          {
            hotel.location&&<div className="location-container"> 
              {
                hotel.location.lat&&<span className="location"> 
                  {hotel.location.lat}
                </span> 
              }            
              {
                hotel.location.lon&&<span className="location">                
                  {hotel.location.lon}
                </span>   
              }                           
            </div>
          }
        </div>
      </div>
    )
  }
}

export default HotelCard;
